import {
  SfSidebar,
  SfHeading,
  SfButton,
  SfIcon,
  SfProperty,
  SfPrice,
  SfCollectedProduct,
  SfImage,
  SfLink,
  SfNotification,
  SfLoader
} from '@storefront-ui/vue';
import { computed } from '@vue/composition-api';
import { useCart, useUser, cartGetters } from '@vue-storefront/shopify';
import { useUiState, useUiNotification } from '~/composables';
import { onSSR } from '@vue-storefront/core';

export default {
  name: 'Cart',
  components: {
    SfSidebar,
    SfButton,
    SfHeading,
    SfIcon,
    SfProperty,
    SfPrice,
    SfCollectedProduct,
    SfImage,
    SfLink,
    SfNotification,
    SfLoader
  },
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  data() {
    return {
      visible: false,
      isLoadervisible: false,
      productToRemove: ''
    };
  },
  watch: {
    // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
    dataAmount: {
      // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
      handler(n) {
        this.dataAmount = n;
      },
      deep: true
    },
    totals: {
      // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
      handler(n) {
        this.dataAmount = n.subtotal;
      },
      deep: true
    }
  },
  methods: {
    // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
    ConfirmRemove(product) {
      if (this.notifications.length > 0) {
        this.notifications[0].dismiss();
      }
      this.visible = true;
      this.productToRemove = product;
    },
    // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
    async handleRemove(productObj) {
      this.isLoadervisible = true;
      await this.removeItem(productObj).then(() => {
        this.isLoadervisible = false;
        this.visible = false;
        this.sendNotification({
          key: 'product_removed',
          message: `${productObj.product.title} has been successfully removed from your cart.`,
          type: 'success',
          title: 'Product removed!',
          icon: 'check'
        });
      });
    },
    // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
    handleCheckout(checkoutUrl) {
      setTimeout(() => {
        window.location.replace(checkoutUrl);
      }, 400);
    }
  },
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  setup() {
    const { isCartSidebarOpen, toggleCartSidebar } = useUiState();
    const { cart, removeItem, updateItemQty, load: loadCart } = useCart();
    const { isAuthenticated } = useUser();
    const { send: sendNotification, notifications } = useUiNotification();
    const products = computed(() => cartGetters.getItems(cart.value));
    const totals = computed(() => cartGetters.getTotals(cart.value));
    const totalItems = computed(() => cartGetters.getTotalItems(cart.value));
    const checkoutURL = computed(() => cartGetters.getcheckoutURL(cart.value));

    onSSR(async () => {
      await loadCart();
    });

    return {
      isAuthenticated,
      products,
      removeItem,
      updateItemQty,
      isCartSidebarOpen,
      toggleCartSidebar,
      totals,
      totalItems,
      cartGetters,
      checkoutURL,
      sendNotification,
      notifications
    };
  }
};
